import RootStore from 'api/RootApi';
import { ApiResponse, HTTPMethod } from 'api/RootApi/ApiStore/types';

import { ApiResp, ISubscribesApi, SubscribesData, SubscribesResp } from './types';

export default class SubscribesApi implements ISubscribesApi {
	private readonly _apiStore = RootStore.apiStore;

	async emailSend(data: SubscribesData): Promise<ApiResp> {
		const response = await this._apiStore.request<ApiResponse<SubscribesResp, SubscribesResp>>({
			method: HTTPMethod.POST,
			endpoint: `/api/v1/subscribes`,
			headers: {
				accept: 'application/json',
				'Content-Type': 'application/json',
			},
			data,
		});
		try {
			return {
				success: response.success,
				data: response.data,
				status: response.status,
			};
		} catch (e) {
			return {
				success: response.success,
				data: response.data,
				status: response.status,
			};
		}
	}
}
