import Title from 'components/UI/Title';
import React from 'react';
import Product from 'components/ProductTile';

import styles from './Novelties.module.scss';
import Button from 'components/UI/Button';
import Col from 'components/UI/Col';
import ActiveLink from 'components/ActiveLink';
import { useTranslation } from 'next-i18next';
import { productItemDataModel } from 'api/anotherMainPageData/models/AnotherMainPageDataModel';
import { useSelector } from 'react-redux';

type NoveltiesTypes = {
	products: productItemDataModel[];
	buttonText: string;
	noButton?: boolean;
	title: string;
	noPadding?: boolean;
	link: string;
};

const Novelties = ({ products, buttonText, title, link, noPadding, noButton = false }: NoveltiesTypes) => {
	const { t } = useTranslation();
	const isMobile = useSelector((state: any) => state.isMobile.isMobile);
	return (
		<Col className={`${noPadding && styles.noPadding} ${styles.noveltiesCol}`}>
			<>
				{title && title.length > 0 && <Title withDot text={title} />}
				<div
					className={`${styles.novelties} ${
						(products.length < 4 && !isMobile) || (products.length < 2 && isMobile) ? styles.borderTop : ''
					}`}
					style={{
						borderTop:
							(products.length < 4 && !isMobile) || (products.length < 2 && isMobile)
								? 'none'
								: '1px solid rgba(35, 35, 35, 0.3)',
					}}
				>
					{products.map(item => (
						<Product isNovelties key={item.id} data={item} />
					))}
				</div>
				{!noButton && (
					<ActiveLink href={link}>
						<Button className={styles.button} text={buttonText} isInvisible />
					</ActiveLink>
				)}
			</>
		</Col>
	);
};

export default React.memo(Novelties);
