import React from 'react';

import styles from './MainBanner.module.scss';
import { MainBannerModel } from 'api/banner/models/MainBannerModel';
import HeaderBanner from 'components/HeaderBanner';
import { useSelector } from 'react-redux';
import arrowLeft from '../../../../../public/static/img/arrowLeft.svg';
import arrowRight from '../../../../../public/static/img/arrowRight.svg';
import Image from 'next/image';
import { Swiper, SwiperSlide } from 'swiper/react';
import 'swiper/swiper-bundle.min.css';
import 'swiper/swiper.min.css';
import SwiperCore, { Autoplay } from 'swiper';
SwiperCore.use([Autoplay]);
type MainBannerTypes = {
	headerBanner: MainBannerModel;
};

// type Direction = 'next' | 'prev';

const MainBanner = ({ headerBanner }: MainBannerTypes) => {
	// const [imageCount, setImageCount] = React.useState<number>(1);
	const [headerBannerState, setHeaderBannerState] = React.useState<MainBannerModel>(headerBanner);
	const swiperRef = React.useRef(null);

	const MainBannerRef = React.useRef<HTMLDivElement>(null);

	const isMobile = useSelector((state: any) => state.isMobile.isMobile);

	return (
		<div className={styles.MainBannerContainer}>
			{headerBanner.length > 1 && (
				<div className={styles.arrows} style={{ top: isMobile && 195 }}>
					<button
						className={!isMobile ? styles.arrowRight : styles.arrowRightMobile}
						onClick={() => swiperRef.current.slidePrev()}
					>
						<Image src={arrowLeft} alt="<" />
					</button>
					<button
						className={!isMobile ? `${styles.arrowLeft}` : styles.arrowLeftMobile}
						onClick={() => swiperRef.current.slideNext()}
					>
						<Image src={arrowRight} alt=">" />
					</button>
				</div>
			)}

			<div ref={MainBannerRef} className={styles.MainBanner}>
				<Swiper
					slidesPerView={'auto'}
					loop={true}
					autoplay={{
						delay: 7000,
						disableOnInteraction: false,
					}}
					onSwiper={swiper => {
						swiperRef.current = swiper;
					}}
					className={`mySwiper ${styles.swiper}`}
				>
					{Array.isArray(headerBannerState) &&
						headerBannerState.length !== 0 &&
						headerBannerState.map(item => {
							return (
								<SwiperSlide key={item.id}>
									<HeaderBanner
										key={item?.id}
										isNotHeader
										img={isMobile ? item?.bannerImgMobile : item?.bannerImg}
										video={isMobile ? item?.mobileVideo : item?.detailVideo}
										annotation={item?.annotation}
										text={item?.name}
										link={item?.link || ''}
										block={item?.block}
									/>
								</SwiperSlide>
							);
						})}
				</Swiper>
			</div>
		</div>
	);
};

export default React.memo(MainBanner);
