import React, { useMemo } from 'react';

import styles from './Category.module.scss';
import Image, { StaticImageData } from 'next/image';

import arrowDark from 'public/static/img/arrowDark.svg';

import routes from 'helpers/routes';
import ActiveLink from 'components/ActiveLink';
import { useSelector } from 'react-redux';
import { IBMPlexMono, SuisseIntl } from 'helpers/utils/fonts';

type CategoryTypes = {
	name: string;
	img: string | StaticImageData;
	count: number;
	redirectTo: string;
	stop: boolean;
};

const Category = ({ name, img, count, redirectTo, stop }: CategoryTypes) => {
	const isMobile = useSelector((state: any) => state.isMobile.isMobile);
	// Находит ближайший пробел к середине строки и ставить там \n
	const nameFormatted = useMemo(() => {
		if (!name.includes(' ')) {
			return (
				<>
					<p className={SuisseIntl.className}>{name}</p>
					<p>{`(${count})`}</p>
				</>
			);
		}
		let middle = Math.floor(name.length / 2);
		const before = name.lastIndexOf(' ', middle);
		const after = name.indexOf(' ', middle + 1);

		if (before == -1 || (after != -1 && middle - before >= after - middle)) {
			middle = after;
		} else {
			middle = before;
		}

		// return name.substring(0, middle).concat(`\n${name.substring(middle + 1)}`);
		return (
			<p className={SuisseIntl.className}>
				<span className={styles.text}>{name.substring(0, middle)}</span>
				<span className={`${IBMPlexMono.className} ${styles.count}`}>{`(${count})`}</span>
				<br />
				<span className={styles.text}>{name.substring(middle + 1)}</span>
			</p>
		);
	}, [name, count]);

	return (
		<div className={styles.category}>
			<ActiveLink href={routes.catalog + redirectTo}>
				<>
					<Image
						alt={name}
						src={img}
						onLoadingComplete={img => (img.style.backgroundImage = 'none')}
						sizes="100vw"
						fill
						priority
					/>
					<div className={styles.content}>
						{nameFormatted}
						<Image
							alt="arrow"
							src={arrowDark}
							width={isMobile ? 32 : 40}
							height={isMobile ? 32 : 40}
							style={{
								maxWidth: '100%',
								height: 'auto',
							}}
						/>
					</div>
				</>
			</ActiveLink>
		</div>
	);
};

export default React.memo(Category);
