import Button from 'components/UI/Button';
import Col from 'components/UI/Col';
import Title from 'components/UI/Title';
import { SuisseIntl } from 'helpers/utils/fonts';

import Image, { StaticImageData } from 'next/image';
import Link from 'next/link';
import React from 'react';
import { useSelector } from 'react-redux';

import styles from './BannerForYou.module.scss';

type BannerForYouTypes = {
	dotTitle: string;
	title: string;
	text: string;
	buttonText: string;
	img: string | StaticImageData;
	imgMobile: string | StaticImageData;
	link: string;
};

const BannerForYou = ({ dotTitle, title, text, buttonText, img, imgMobile, link }: BannerForYouTypes) => {
	const isMobile = useSelector((state: any) => state.isMobile.isMobile);
	return (
		<Col className={styles.bannerCol}>
			<div className={styles.banner}>
				<div className={styles.content}>
					<Title text={dotTitle} withDot />
					<h2 className={SuisseIntl.className}>{title}</h2>
					<p>{text}</p>
					<Link href={link}>
						<Button text={buttonText} isInvisible />
					</Link>
				</div>
				{!isMobile ? (
					<Image
						alt={title}
						src={img}
						width={440}
						height={412}
						quality={100}
						style={{
							maxWidth: '100%',
							height: 'auto',
						}}
					/>
				) : (
					<Image alt={title} src={imgMobile} sizes="100vw" fill quality={100} />
				)}
			</div>
		</Col>
	);
};

export default React.memo(BannerForYou);
