import { SuisseIntl } from 'helpers/utils/fonts';
import React from 'react';

import styles from './OrderInfo.module.scss';

type OrderInfoTypes = {
	data: {
		name: string;
		info: string;
	}[];
};

const OrderInfo = ({ data }: OrderInfoTypes) => {
	return (
		<div className={styles.orderInfo}>
			{data &&
				data.map(item => {
					return (
						<div key={item.name} className={styles.block}>
							<p>{item.name}</p>
							<p className={SuisseIntl.className}>{item.info}</p>
						</div>
					);
				})}
		</div>
	);
};

export default React.memo(OrderInfo);
