import Col from 'components/UI/Col';
import Title from 'components/UI/Title';
import { SuisseIntl } from 'helpers/utils/fonts';

import Image, { StaticImageData } from 'next/image';
import Link from 'next/link';
import arrowDark from 'public/static/img/arrowDark.svg';
import React from 'react';
import { useSelector } from 'react-redux';

import styles from './WhatToRead.module.scss';

type WhatToReadTypes = {
	dotTitle: string;
	date: string;
	title: string;
	img: StaticImageData | string;
	link: string;
};

const WhatToRead = ({ dotTitle, date, title, img, link }: WhatToReadTypes) => {
	const isMobile = useSelector((state: any) => state.isMobile.isMobile);

	return (
		<Col /*marginBottom={isMobile ? 0 : 50}*/>
			<div className={styles.banner}>
				{isMobile ? (
					<Image alt={dotTitle} src={img} fill sizes="100vw" quality={100} />
				) : (
					<Image
						alt={dotTitle}
						src={img}
						width={584}
						height={432}
						quality={100}
						style={{
							maxWidth: '100%',
							height: 'auto',
						}}
					/>
				)}
				<div className={styles.info}>
					<Title text={dotTitle} withDot />
					<div className={styles.data}>
						<p>{date}</p>
						{!isMobile && (
							<Link href={link}>
								<p className={SuisseIntl.className}>{title}</p>
							</Link>
						)}
					</div>
				</div>
				{isMobile && (
					<Link href={link}>
						<p className={SuisseIntl.className}>{title}</p>
					</Link>
				)}
				<Image
					alt="arrow"
					src={arrowDark}
					width={isMobile ? 60 : 96}
					height={isMobile ? 60 : 96}
					style={{
						maxWidth: '100%',
						height: 'auto',
					}}
				/>
			</div>
		</Col>
	);
};

export default React.memo(WhatToRead);
