import React from 'react';
import Home from 'components/pages/Home';
import { serverSideTranslations } from 'next-i18next/serverSideTranslations';
import Header from 'components/Header';
import Footer from 'components/Footer';
import CategoriesListApi from 'api/categories/CategoriesApi';
import BannerNavApi from 'api/banner/BannerApi';
import AnotherMainPageApi from 'api/anotherMainPageData/AnotherMainPageData';
import useAppDispatch from 'helpers/hooks/useAppDispatch';
import { changeIsMobile } from 'store/isMobile/IsMobileStore';
import { useGetUserData } from 'helpers/hooks/useGetUserData';
import { baseSSR } from 'helpers/utils/baseSSR';
import Head from 'next/head';
import MainBannerApi from 'api/banner/MainBannerApi';
import TopBannerApi from 'api/banner/TopBannerApi';
import { HeaderBanner } from 'components/Header/HeaderBanner';

const HomePage = ({ data }) => {
	const dispatch = useAppDispatch();

	useGetUserData();

	dispatch(changeIsMobile(data.isMobile));

	return (
		<>
			<Head>
				{process.env.NEXT_PUBLIC_IS_PRODUCTION === 'true' && (
					<meta name="yandex-verification" content="adc08adac12691da" />
				)}
			</Head>
			<HeaderBanner data={data.topBannerData} />
			<Header />
			<Home
				headerBanner={data.bannerTop}
				bannerCenter={data.bannerCenter}
				bannerBottom={data.bannerBottom}
				bannerAdditional={data.bannerAdditional}
				categories={data.categories}
				anotherMainPageData={data.anotherMainPageData}
			/>
			<Footer withDarkBGC={false} />
		</>
	);
};

export const getServerSideProps = async ({ req, locale }) => {
	const { isMobile } = await baseSSR(req);
	const categoriesListApi = new CategoriesListApi();
	const bannerNavApi = new BannerNavApi();
	const mainBannerApi = new MainBannerApi();
	const anotherMainPageApi = new AnotherMainPageApi();
	const topBanner = new TopBannerApi();
	const [categories, bannerTop, bannerCenter, bannerBottom, bannerAdditional, anotherMainPageData, topBannerData] =
		await Promise.all([
			categoriesListApi.getCategoriesListData(),
			mainBannerApi.getMainBanner(),
			bannerNavApi.getBannerData({ location: 'Главная центр' }),
			bannerNavApi.getBannerData({ location: 'Главная низ' }),
			bannerNavApi.getBannerData({ location: 'Главная дополнительный' }),
			anotherMainPageApi.getAnotherMainPageData(),
			topBanner.getTopBanner(),
		]);
	return {
		props: {
			data: {
				bannerTop: bannerTop.data,
				bannerCenter: bannerCenter.data,
				bannerBottom: bannerBottom.data,
				bannerAdditional: bannerAdditional.data,
				categories: categories.data,
				anotherMainPageData: anotherMainPageData.data,
				topBannerData: topBannerData.data,
				isMobile,
			},
			...(await serverSideTranslations(locale, ['common', 'home'])),
		},
	};
};

export default HomePage;
