import React from 'react';
import Image from 'next/image';
import emailArrow from '/public/static/img/emailArrow.svg';
import emailSuccess from '/public/static/img/emailSuccess.svg';

import styles from './EmailBlock.module.scss';
import Col from 'components/UI/Col';

import { useTranslation } from 'next-i18next';
import { useSelector } from 'react-redux';
import SubscribesApi from 'api/subscribes/SubscribesApi';

const subscribesApi = new SubscribesApi();

const EmailBlock = () => {
	const [emailValue, setEmailValue] = React.useState<string>('');
	const [isOk, setIsOk] = React.useState<boolean>(false);
	const [errorMessage, setErrorMessage] = React.useState<string>('');
	const isMobile = useSelector((state: any) => state.isMobile.isMobile);

	const { t } = useTranslation();

	const onChangeEmailValue = (event: React.ChangeEvent<HTMLInputElement>) => {
		const { target } = event;

		setEmailValue(target.value);
	};

	const submitSearchValue = () => {
		subscribesApi.emailSend({ email: emailValue }).then(res => {
			if (res.data.message === 'ok') {
				setIsOk(true);
			}
			if (res.data.errors && res.data.errors.email) {
				setErrorMessage(res.data.errors.email[0]);
			}
		});
	};

	const onKeyDownSearchValueHandler = (event: React.KeyboardEvent) => {
		if (event.code !== 'Enter') return;
		subscribesApi.emailSend({ email: emailValue }).then(res => {
			if (res.data.message === 'ok') {
				setIsOk(true);
			}
			if (res.data.errors && res.data.errors.email) {
				setErrorMessage(res.data.errors.email[0]);
			}
		});
	};

	return (
		<Col marginBottom={isMobile ? 40 : 60}>
			<div className={styles.email}>
				{isOk ? (
					<p>Спасибо за подписку! Письмо со скидкой уже ждет вас на почте!</p>
				) : errorMessage ? (
					<p>{errorMessage}</p>
				) : (
					<p>{t('home:emailBlock.description')}</p>
				)}
				<div className={styles.input}>
					<input
						autoComplete="off"
						className={isOk ? styles.isOk : ''}
						onKeyDown={onKeyDownSearchValueHandler}
						placeholder={isOk ? emailValue : t('home:emailBlock.placeholder')}
						onChange={onChangeEmailValue}
						value={isOk ? '' : emailValue}
						disabled={isOk}
					/>
					{isOk ? (
						<Image
							alt="success"
							src={emailSuccess}
							width={isMobile ? 60 : 96}
							height={isMobile ? 60 : 96}
							style={{
								maxWidth: '100%',
								height: 'auto',
							}}
						/>
					) : (
						<Image
							alt="arrow"
							onClick={submitSearchValue}
							src={emailArrow}
							width={isMobile ? 60 : 96}
							height={isMobile ? 60 : 96}
							style={{
								maxWidth: '100%',
								height: 'auto',
							}}
						/>
					)}
				</div>
			</div>
		</Col>
	);
};

export default React.memo(EmailBlock);
