import styles from './Header.module.scss';
import Image from 'next/image';
import close from '/public/static/img/close.svg';
import { useEffect, useState } from 'react';

export const HeaderBanner = ({ data }: any) => {
	const [isShow, setIsShow] = useState(true);
	const [isHidden, setHidden] = useState(false);
	const toggle = () => setHidden(true);

	useEffect(() => {
		if (isHidden) {
			setTimeout(() => setIsShow(false), 400);
		}
	}, [isHidden]);
	const bannerCn = [styles.banner, isHidden ? styles.banner_hide : ''].join(' ');
	if (!isShow || !data) return null;
	const content = (data && data.data[data.data.length - 1]?.text) || '';
	return (
		<div className={bannerCn}>
			<div className={styles.banner_text} dangerouslySetInnerHTML={{ __html: content }} />

			<button onClick={toggle}>
				<Image
					alt="X"
					src={close}
					width={24}
					height={24}
					style={{
						maxWidth: '100%',
						height: 'auto',
					}}
				/>
			</button>
		</div>
	);
};
