import { CategoriesListDataModel } from 'api/categories/models/CategoriesModel';
import ActiveLink from 'components/ActiveLink';
import Button from 'components/UI/Button';
import Col from 'components/UI/Col';
import Title from 'components/UI/Title';

import routes from 'helpers/routes';
import { useTranslation } from 'next-i18next';
import React from 'react';
import { useSelector } from 'react-redux';
import Category from '../Category';

import styles from './ByCategory.module.scss';

type ByCategoryTypes = {
	categories: CategoriesListDataModel;
};

const ByCategory = ({ categories }: ByCategoryTypes) => {
	const [left, setLeft] = React.useState<number>(0);
	const [drag, setDrag] = React.useState<boolean>(false);
	const [coorX, setCoorX] = React.useState<number>(0);

	const scroll = React.useRef<HTMLDivElement>(null);

	const isMobile = useSelector((state: any) => state.isMobile.isMobile);

	const { t } = useTranslation();

	const onMousedownHandler = (event: React.MouseEvent<HTMLDivElement>) => {
		setDrag(true);
		setCoorX(event.pageX - event.currentTarget.offsetLeft);
	};

	const onMouseupHandler = () => {
		setDrag(false);
		setLeft(scroll.current.scrollLeft);
	};

	const onMousemoveHandler = (event: React.MouseEvent<HTMLDivElement>) => {
		if (drag) {
			event.currentTarget.scrollLeft = left - (event.pageX - event.currentTarget.offsetLeft - coorX);
		}
		event.preventDefault();
	};

	const onDragstartHandler = (event: React.MouseEvent) => {
		event.preventDefault();
	};

	return (
		<Col className={styles.categoriesCol}>
			<>
				<Title text={t('home:titles.categories')} withDot />
				<div
					ref={scroll}
					onMouseDown={!isMobile ? onMousedownHandler : null}
					onMouseUp={!isMobile ? onMouseupHandler : null}
					onMouseMove={!isMobile ? onMousemoveHandler : null}
					onDragStart={!isMobile ? onDragstartHandler : null}
					className={styles.categories}
				>
					{categories.map(item => (
						<Category
							redirectTo={item.redirectTo}
							key={item.name}
							name={item.name}
							img={item.img}
							count={item.count}
							stop={item.stop}
						/>
					))}
				</div>
				<ActiveLink href={routes.categories + 'zhenshchiny'}>
					<Button className={styles.button} text={t('home:buttons.viewAll')} isInvisible />
				</ActiveLink>
			</>
		</Col>
	);
};

export default React.memo(ByCategory);
