import React from 'react';
import styles from './ModalWindow.module.scss';
import Image from 'next/image';
import { useSelector } from 'react-redux';

type ModalWindowTypes = {
	isOpen: boolean;
	onClose: () => void;
	children: React.ReactElement<any, any>;
	light?: boolean;
};

const ModalWindow = ({ isOpen, onClose, children, light = false }: ModalWindowTypes) => {
	const modalRef = React.useRef<HTMLDivElement | null>(null);

	const isMobile = useSelector((state: any) => state.isMobile.isMobile);

	const onCloseDetails = (event: React.MouseEvent<HTMLDivElement>) => {
		const { target } = event;

		if (!modalRef.current?.contains(target as Node)) {
			onClose();
		}
	};

	React.useEffect(() => {
		if (isOpen) {
			document.querySelector('body').style.overflowY = 'hidden';
		} else {
			document.querySelector('body').style.overflowY = 'scroll';
		}
	}, [isOpen]);

	return (
		<>
			{isOpen && (
				<div className={styles.modalContainer} onClick={onCloseDetails}>
					<div ref={modalRef} className={`${styles.modal} ${light ? styles.light : ''}`}>
						{light ? (
							<Image
								src="/static/img/closeDark.svg"
								alt="X"
								width={isMobile ? 20 : 46}
								height={isMobile ? 20 : 46}
								onClick={onClose}
							/>
						) : (
							<Image
								src="/static/img/close.svg"
								alt="X"
								width={isMobile ? 20 : 46}
								height={isMobile ? 20 : 46}
								onClick={onClose}
							/>
						)}
						{children}
					</div>
				</div>
			)}
		</>
	);
};

export default React.memo(ModalWindow);
