import Button from 'components/UI/Button';
import Image, { StaticImageData } from 'next/image';
import React, { useMemo } from 'react';
import { IBMPlexMono, SuisseIntl } from 'helpers/utils/fonts';
import arrowBanner from '../../public/static/img/arrowBanner.svg';

import styles from './HeaderBanner.module.scss';
import DOMPurify from 'isomorphic-dompurify';

import Link from 'next/link';
import { useSelector } from 'react-redux';
import { useRouter } from 'next/router';
import routes from 'helpers/routes';

type HeaderBannerTypes = {
	img: StaticImageData | string;
	video?: string;
	text: string;
	link?: string;
	isBlackBtn?: boolean;
	wholeSale?: boolean;
	isInvisibleBtn?: boolean;
	annotation?: string;
	isNotHeader?: boolean;
	isAllWidth?: boolean;
	className?: string;
	aboutKids?: boolean;
	block?: {
		color: string;
		altColor: string;
	};
	onClick?: () => void;
};

const HeaderBanner = ({
	img,
	video,
	text,
	link,
	isBlackBtn,
	isInvisibleBtn,
	wholeSale,
	aboutKids,
	annotation,
	isNotHeader,
	isAllWidth,
	className,
	onClick,
	block,
}: HeaderBannerTypes) => {
	const [isHover, setIsHover] = React.useState<boolean>(false);

	const isMobile = useSelector((state: any) => state.isMobile.isMobile);

	const router = useRouter();

	// Перейти на новый маршрут
	const navigateToRoute = linkTo => {
		if (aboutKids) {
			return;
		}
		router.replace(linkTo);
	};

	const navigateFromAboutKids = linkTo => {
		router.push(linkTo);
	};

	const hoverHandler = () => {
		if (isMobile) return;
		setIsHover(prev => !prev);
	};

	const onTouchHandler = () => {
		setIsHover(prev => !prev);
	};
	return (
		<div
			className={`${styles.HeaderBanner} ${isNotHeader ? styles.notHeader : ''} ${aboutKids ? styles.aboutKids : ''} ${
				isAllWidth ? styles.isAllWidth : ''
			} ${className ? className : ''} HeaderBanner`}
			onClick={onClick || null}
		>
			{isNotHeader ? (
				<>
					{video ? (
						<video
							preload="auto"
							autoPlay
							muted
							playsInline
							loop
							poster={isMobile ? '/static/img/bannerMobileLoader.svg' : '/static/img/bannerLoader.svg'}
						>
							<source src={video} type="video/mp4"></source>
						</video>
					) : (
						<Image
							alt={text}
							src={img}
							sizes="100vw"
							fill
							priority
							quality={100}
							onClick={() => navigateToRoute(link ? link : null)}
						/>
					)}
				</>
			) : (
				<Image
					alt={text}
					width={413}
					height={220}
					src={img}
					priority
					quality={100}
					style={{
						maxWidth: '100%',
						height: 'auto',
					}}
					onClick={() => navigateToRoute(link ? link : null)}
				/>
			)}
			{aboutKids && (
				<>
					<span className={styles.bread_kids} onClick={() => navigateFromAboutKids(routes.home)}>
						<p className={IBMPlexMono.className}>О бренде / Главная</p>
					</span>
					<div className={styles.about_kids}>
						<p className={`${SuisseIntl.className} ${styles.title_text}`}>
							TOTTI KIDS — БРЕНД УДОБНОЙ <br></br> И СТИЛЬНОЙ ОДЕЖДЫ
						</p>
						<p className={`${IBMPlexMono.className} ${styles.bottom_text}`}>
							из высокотехнологичных тканей <br></br>
							для детей от 3 до 9 лет{' '}
						</p>
						<Button
							className={styles.about_btn}
							isInvisible
							text={text}
							onClick={() => router.push('/catalog/deti')}
						/>
					</div>
				</>
			)}
			{isNotHeader && !aboutKids ? (
				<Link href={link ? link : ''}>
					<button
						className={`${!wholeSale ? styles.headerButton : styles.wholeSale} ${
							isBlackBtn ? styles.isBlackBtn : null
						}`}
					>
						<p
							className={SuisseIntl.className}
							style={isMobile && isHover ? { color: isBlackBtn ? '#F3F3EA' : '#1B2530' } : {}}
							dangerouslySetInnerHTML={{ __html: DOMPurify.sanitize(text) }}
						/>
						{!wholeSale ? (
							<p
								className={IBMPlexMono.className}
								style={isMobile ? { top: '-8px', left: '-220px' } : {}}
								dangerouslySetInnerHTML={{ __html: DOMPurify.sanitize(annotation) }}
							/>
						) : (
							<Image src={arrowBanner} alt="->" />
						)}
					</button>
				</Link>
			) : aboutKids ? (
				<></>
			) : (
				<Link href={link}>
					<Button isWhite isInvisible text={text} />
				</Link>
			)}
		</div>
	);
};

export default React.memo(HeaderBanner);
