import HeaderBanner from 'components/HeaderBanner';
import React, { useEffect } from 'react';
import Content from 'components/UI/Content';
import Novelties from './views/Novelties';
import BannerForYou from './views/BannerForYou';
import ByCategory from './views/ByCategory';
import Col from 'components/UI/Col';
import OrderInfo from './views/OrderInfo';
import WhatToRead from './views/WhatToRead';
import EmailBlock from './views/EmailBlock';
import SubscribesApi from 'api/subscribes/SubscribesApi';
import emailSuccess from '/public/static/img/checkbox.svg';
import Image from 'next/image';

import { CategoriesListDataModel } from 'api/categories/models/CategoriesModel';
import { BannerDataModel } from 'api/banner/models/BannerModel';
import { AnotherMainPageDataModel } from 'api/anotherMainPageData/models/AnotherMainPageDataModel';
import { useSelector } from 'react-redux';

import styles from './Home.module.scss';
import { MainBannerModel } from 'api/banner/models/MainBannerModel';
import MainBanner from './views/MainBanner';
import ModalWindow from 'components/UI/ModalWindow';
import { useTranslation } from 'react-i18next';

type HomeTypes = {
	categories: CategoriesListDataModel;
	headerBanner: MainBannerModel;
	bannerCenter: BannerDataModel;
	bannerBottom: BannerDataModel;
	bannerAdditional: BannerDataModel;
	anotherMainPageData: AnotherMainPageDataModel;
};

const Home = ({ categories, headerBanner, bannerCenter, bannerBottom, bannerAdditional, anotherMainPageData }: HomeTypes) => {
	const isMobile = useSelector((state: any) => state.isMobile.isMobile);
	const [isOpenModal, setIsOpenModal] = React.useState<boolean>(false);

	const [emailValue, setEmailValue] = React.useState<string>('');
	const [isOk, setIsOk] = React.useState<boolean>(false);
	const [errorMessage, setErrorMessage] = React.useState<string>('');

	useEffect(() => {
		if (!localStorage.getItem('subscriptionModalShown')) {
			setIsOpenModal(true);
			localStorage.setItem('subscriptionModalShown', '1');
		}
	}, []);

	const { t } = useTranslation();
	const subscribesApi = new SubscribesApi();

	const onChangeEmailValue = (event: React.ChangeEvent<HTMLInputElement>) => {
		const { target } = event;

		setEmailValue(target.value);
	};

	const submitSearchValue = () => {
		subscribesApi.emailSend({ email: emailValue }).then(res => {
			if (res.data.message === 'ok') {
				setIsOk(true);
			}
			if (res.data.errors && res.data.errors.email) {
				setErrorMessage(res.data.errors.email[0]);
			}
		});
	};

	const onKeyDownSearchValueHandler = (event: React.KeyboardEvent) => {
		if (event.code !== 'Enter') return;
		subscribesApi.emailSend({ email: emailValue }).then(res => {
			if (res.data.message === 'ok') {
				setIsOk(true);
			}
			if (res.data.errors && res.data.errors.email) {
				setErrorMessage(res.data.errors.email[0]);
			}
		});
	};

	const toggleModal = () => {
		setIsOpenModal(prev => !prev);
	};

	return (
		<>
			<MainBanner headerBanner={headerBanner} />
			<ModalWindow isOpen={isOpenModal} onClose={toggleModal} light>
				<div className={styles.email}>
					<h2 suppressHydrationWarning>{t('home:emailModal.title')}</h2>
					{isOk ? (
						<p>Спасибо за подписку! Письмо со скидкой уже ждет вас на почте!</p>
					) : errorMessage ? (
						<p>{errorMessage}</p>
					) : (
						<p suppressHydrationWarning>{t('home:emailModal.description')}</p>
					)}
					<div className={styles.input}>
						<input
							autoComplete="off"
							className={isOk ? styles.isOk : ''}
							onKeyDown={onKeyDownSearchValueHandler}
							placeholder={isOk ? emailValue : 'Email'}
							onChange={onChangeEmailValue}
							suppressHydrationWarning
							value={isOk ? '' : emailValue}
							disabled={isOk}
						/>
						{isOk ? (
							<Image
								alt="success"
								src={emailSuccess}
								width={isMobile ? 20 : 35}
								height={isMobile ? 20 : 35}
								style={{
									maxWidth: '100%',
									height: 'auto',
								}}
							/>
						) : (
							<span onClick={submitSearchValue}>Подписаться</span>
						)}
					</div>
				</div>
			</ModalWindow>
			<Content className={styles.content}>
				<>
					{anotherMainPageData.productsBlock &&
						Array.isArray(anotherMainPageData.productsBlock.data) &&
						anotherMainPageData.productsBlock.data.length !== 0 && (
							<Novelties
								buttonText={anotherMainPageData.productsBlock.buttonName}
								link={anotherMainPageData.productsBlock.buttonUrl}
								title={anotherMainPageData.productsBlock.blockName}
								products={anotherMainPageData.productsBlock.data}
							/>
						)}
					<BannerForYou
						dotTitle={bannerCenter.annotation}
						title={bannerCenter.name}
						text={bannerCenter.additionalText}
						buttonText={bannerCenter.buttonText}
						img={bannerCenter.bannerImg}
						imgMobile={bannerCenter.bannerImgMobile}
						link={bannerCenter.link || ''}
					/>
					{Array.isArray(categories) && categories.length !== 0 && <ByCategory categories={categories} />}
					<Col>
						<HeaderBanner
							isAllWidth
							isNotHeader
							img={isMobile ? bannerBottom.bannerImgMobile : bannerBottom.bannerImg}
							video={isMobile ? bannerBottom.mobileVideo : bannerBottom.detailVideo}
							annotation={bannerBottom.annotation}
							text={bannerBottom.text}
							link={bannerBottom.link || ''}
						/>
					</Col>
					<Col>
						<OrderInfo data={anotherMainPageData.infoBlock} />
					</Col>
					<WhatToRead
						dotTitle={bannerAdditional.annotation}
						date={bannerAdditional.additionalText}
						title={bannerAdditional.text}
						img={isMobile ? bannerAdditional.bannerImgMobile : bannerAdditional.bannerImg}
						link={bannerAdditional.link || ''}
					/>
					<EmailBlock />
				</>
			</Content>
		</>
	);
};

export default React.memo(Home);
